(function () {
    'use strict';

    angular
        .module('ghecko')
        .controller('ProfileController', ProfileController);


    function ProfileController(ProfileService, $stateParams, $mdToast) {
        var vm = this;
        vm.userId = $stateParams.id;
        function init() {
            ProfileService.getInformation(vm.userId).success(function (data) {
                vm.currentUser = data;
                vm.foll = data.followers;
                validateUser();

            });

            ProfileService.getPost(vm.userId)
                .success(function (data) {
                    vm.posts = data;
                })
        }

        init();

        function validateUser() {
            if (localStorage.getItem('userId') == vm.userId) {
                vm.btnEdit = true;
                vm.btnFollow = false;
                vm.btnFollower = false;
            } else {
                vm.btnEdit = false;

                if (vm.foll.length == 0) {
                    vm.btnFollow = true;
                    vm.btnFollower = false;
                } else {
                    for (var i = 0; i < vm.foll.length; i++) {
                        if (localStorage.getItem('userId') == vm.foll[i].userId) {
                            vm.btnFollow = false;
                            vm.btnFollower = true;
                        } else {
                            vm.btnFollow = true;
                            vm.btnFollower = false;
                        }
                    }
                }
            }
        }

        vm.follow = function (puser) {
            var follower = {
                userId: localStorage.getItem('userId'),
                photo: localStorage.getItem('profilephoto'),
                username: localStorage.getItem('username')
            };
            puser.followers.push(follower);
            puser.cantseguidores = puser.cantseguidores + 1;
            ProfileService.updateInformation(puser)
                .success(function (data) {
                    init();
                    $mdToast.show(
                        $mdToast.simple()
                            .textContent('Following!')
                            .position('top', 'right')
                            .hideDelay(3000)
                    );
                })
            countFollowers();
        }

        vm.unfollow = function (puser) {
            var meuser = localStorage.getItem('userId');
            for (var i = 0; i < puser.followers.length; i++) {
                if (puser.followers[i].userId == meuser) {
                    puser.followers.splice(i, 1);
                }
            }
            puser.cantseguidores = puser.cantseguidores - 1;
            ProfileService.updateInformation(puser)
                .success(function (data) {
                    init();
                    $mdToast.show(
                        $mdToast.simple()
                            .textContent('Unfollow!')
                            .position('top', 'right')
                            .hideDelay(3000)
                    );
                })

            discountFollowers();

        }

        function countFollowers() {
            ProfileService.getInformation(localStorage.getItem('userId'))
                .success(function (user) {
                    user.cantseguidos = user.cantseguidos + 1;
                    ProfileService.updateInformation(user)
                        .success(function () {

                        })
                })
        }

        function discountFollowers() {
            ProfileService.getInformation(localStorage.getItem('userId'))
                .success(function (user) {
                    user.cantseguidos = user.cantseguidos - 1;
                    ProfileService.updateInformation(user)
                        .success(function () {


                        })


                })

        }

    }
})();



